<script>
import { defineComponent } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUsers from 'stores/user';
import useApp from 'stores/app';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import SelectCountry from 'src/components/Helpers/SelectCountry.vue';
import SelectRoles from 'src/components/Helpers/SelectRoles.vue';
import SelectEmployeeCount from 'src/components/Helpers/SelectEmployeeCount.vue';
import GrowmodoTestimonials from 'src/components/GrowmodoTestimonials.vue';
import {
  extractErrorMessageString,
  updateStepperHeader,
} from 'src/assets/scripts/functions';
import { date } from 'quasar';
import fnStore from 'stores/fn.store';

export default defineComponent({
  name: 'SignupPage',

  components: {
    GrowmodoTextIcon,
    SelectCompanyType,
    InputWebsite,
    InputPassword,
    SelectCountry,
    SelectRoles,
    SelectEmployeeCount,
    GrowmodoTestimonials,
  },

  data() {
    return {
      title: 'Signup',
      rightDrawer: false,
      account: {},
      company: {},
      subscription: {},
      formStep: 1,
      stepperHeaderElements: null,
      signupVerifyEmailStatus: false,
      signup_token: null,
      invite_token: null,
      verify_email: '',
      init_step: null,
      last_verification_sent: null,
      getAuth: fnStore.getAuth,
    };
  },

  watch: {
    formStep: {
      async handler(val) {
        this.stepperHeaderStyle();
        if (val > 1 && !this.accountPending) this.rightDrawer = false;
        else this.rightDrawer = true;
      },
    },

    accountPending: {
      async handler(val) {
        if (val) this.rightDrawer = true;
      },
    },

    signupVerifyEmailStatus: {
      async handler(val) {
        setTimeout(() => {
          this.stepperHeaderElements =
            this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
          this.stepperHeaderStyle();
        }, 100);
      },
    },

    signup_token: {
      async handler(val) {
        if (val) {
          // Verify token
          const response = await this.signupVerifyEmail(val);
          const message = extractErrorMessageString(response?.message);
          if (response.success) {
            this.$q.notify({
              icon: 'icon-check-circle-broken',
              message: message || 'Your email has been verified',
            });
            // Go to step 2
            this.$router.push({
              path: '/signup',
              query: {
                verify_email: response.data?.user?.email,
                step: 2,
              },
            });
          } else {
            // Else go back to signup
            this.$q.notify({
              icon: 'icon-alert-triangle',
              iconColor: 'negative',
              message: message || 'Error verifying signup token',
            });
            this.goToSignup();
          }
          this.signupVerifyEmailStatus = false;
        }
      },
    },

    ['$route.query.verify_email']: {
      async handler(val) {
        if (val) {
          this.verify_email = val;
        }
      },
    },

    ['$route.query.step']: {
      async handler(val) {
        if (val) this.init_step = val;
      },
    },

    init_step: {
      async handler(val) {
        if (val && this.user?.token) {
          if (this.signupStep && !isNaN(this.signupStep)) {
            this.prefillForm();
            this.signupVerifyEmailStatus = false;
            this.formStep = this.signupStep;
          }
        }
      },
    },

    verify_email: {
      async handler(val) {
        if (val) {
          if (this.user?.token && this.signupStep === 'verify-email') {
            this.$q.notify({
              icon: 'icon-check-circle-broken',
              message: 'Please verify your email address',
            });
            // Set the Status
            this.signupVerifyEmailStatus = true;
            this.prefillForm();
          } else if (!this.init_step) {
            // Set the Status
            this.signupVerifyEmailStatus = false;
            this.goToSignup();
          }
        }
      },
    },
  },

  computed: {
    ...mapState(useGrowmodo, [
      'externalLinks',
      'companyTypes',
      'subscription_BillingTypes',
    ]),
    ...mapState(useUsers, ['user', 'selectedOrg']),

    listCompanyTypes() {
      return Object.values(this.companyTypes);
    },

    signupStep() {
      return this.onSignupStep(this.user?.signup_step);
    },

    accountPending() {
      return this.user?.status === 'pending' && !this.signupStep;
    },

    verificationReadyInSec() {
      return this.last_verification_sent
        ? date.getDateDiff(
            date.addToDate(this.last_verification_sent, { seconds: 59 }),
            this.$root.realtimeDate,
            'seconds'
          ) || 0
        : -1;
    },

    isVerificationReady() {
      return this.verificationReadyInSec < 0;
    },
  },

  beforeMount() {
    this.initialize();
  },

  async mounted() {
    if (!fnStore.getAuth()) {
      this.$root.setMeta({ title: this.title });
      this.stepperHeaderElements =
        this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
      this.stepperHeaderStyle();
      const token = this.$route.params?.signup_token;
      if (token) {
        console.log('i am ,=mounted');
        this.signupVerifyEmailStatus = true;
        this.signup_token = token;
      } else if (!this.$route.params?.invite_token) {
        const email_to_verify = this.$route.query?.verify_email;
        if (email_to_verify) this.verify_email = email_to_verify;
        const has_init_step = this.$route.query?.step;
        if (has_init_step) this.init_step = has_init_step;
      }
    }
  },

  methods: {
    ...mapActions(useApp, ['init_Signup']),
    ...mapActions(useGrowmodo, ['planPriceCalculator']),
    ...mapActions(useUsers, [
      'signupStep1',
      'signupStep2',
      'signupStep3',
      'onSignupStep',
      'signupVerifyEmail',
      'signupResendEmailVerify',
      'acceptInvitation',
      'getUserRole',
      'resendEmailInvitation',
    ]),

    async initialize() {
      if (fnStore.getAuth()) {
        this.$router.replace('/');
      } else {
        // Signup or Invite
        const invite_token = this.$route.params?.invite_token;
        if (invite_token) {
          this.invite_token = invite_token;
          this.init_Invitation();
        } else {
          this.init_Signup();
        }
      }
    },

    async init_Invitation() {
      console.log('i am ,=init invite');
      this.signupVerifyEmailStatus = true;
      const response = await this.acceptInvitation(this.invite_token);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: message || 'Your email has been verified',
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error accepting invitation',
        });
      }
      this.$router.push({
        path: '/',
      });
    },

    goToSignup() {
      this.formStep = 1;
      this.$router.push('/signup');
    },

    back2home() {
      window.location.href = '/';
    },

    prefillForm() {
      // Prefil the signup form
      this.account = this.user;
      this.company = this.selectedOrg;
    },

    stepperHeaderStyle() {
      if (this.stepperHeaderElements) {
        updateStepperHeader(this.stepperHeaderElements, this.formStep);
      } else {
        this.stepperHeaderElements =
          this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
      }
    },

    previousStep() {
      this.prefillForm();
      this.$refs?.['formStepper']?.previous?.();
    },

    nextStep() {
      this.$refs?.['formStepper']?.next?.();
    },

    async step1() {
      const _ = this.account;
      const response = await this.signupStep1({
        email: _.email,
        firstname: _.firstname,
        lastname: _.lastname,
        password: _.password,
      });
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.$router.push({
          path: '/signup',
          query: {
            verify_email: response.data?.email,
          },
        });
      } else {
        this.signupVerifyEmailStatus = false;
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error: Please check your data',
        });
      }
    },

    async step2() {
      const _ = this.company;
      const payload = {
        business_name: _.title,
        country: _.country,
        company_type_id: _.company_type_id,
        organization_role: this.account.organization_role,
        employee_count_id: _.employee_count_id,
      };
      if (_.website) payload.website = _.website;
      const response = await this.signupStep2(payload, this.user.token);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.prefillForm();
        this.formStep = 3;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error: Please check your data',
        });
      }
    },

    async step3() {
      const _ = this.company;
      const response = await this.signupStep3(
        {
          subscription_billing_id: this.subscription.plan?.id,
          subscription_talent_id: 1,
        },
        this.user.token
      );
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message:
            message || 'Our sales reprecentative will contact you via email',
        });
        // Back to Home if not 'pending'
        if (!this.accountPending) {
          setTimeout(() => {
            this.back2home();
          }, 2000);
        }
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error: Please check your data',
        });
      }
    },

    async resendVerificationLink() {
      if (!this.last_verification_sent || this.isVerificationReady) {
        this.last_verification_sent = Date.now();
        const invited = this.getUserRole() ? true : false;
        console.log(this.getUserRole(), ' +++ Role');
        const response = invited
          ? await this.resendEmailInvitation(this.account?.email)
          : await this.signupResendEmailVerify(this.account?.email);
        const message = extractErrorMessageString(response?.message);
        if (response.success) {
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message:
              message ||
              (invited ? 'Invitation' : 'Verification') + ' link has been sent',
          });
        } else {
          this.last_verification_sent = null;
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message:
              message ||
              'Failed sending ' +
                (invited ? 'invitation' : 'verification') +
                ' link',
          });
        }
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Please wait for a while before trying again',
        });
      }
    },
  },
});
</script>
